/* BREAKPOINTS */
@font-face {
  font-family: "wild-icons";
  src: url("../fonts/wild-icons.eot");
  src: url("../fonts/wild-icons.eot?#iefix") format("eot"), url("../fonts/wild-icons.woff2") format("woff2"), url("../fonts/wild-icons.woff") format("woff"), url("../fonts/wild-icons.ttf") format("truetype"), url("../fonts/wild-icons.svg#wild-icons") format("svg");
}

.icon-facebook:before, .icon-gplus:before, .icon-right:before, .icon-search:before, .icon-twitter:before {
  font-family: "wild-icons";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-decoration: none;
  text-transform: none;
}

.icon-facebook:before {
  content: "";
}

.icon-gplus:before {
  content: "";
}

.icon-right:before {
  content: "";
}

.icon-search:before {
  content: "";
}

.icon-twitter:before {
  content: "";
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.nav {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  align-items: center;
}

.nav__menu {
  display: flex;
  margin: 0;
  padding: 0;
  flex: 1 0 auto;
  justify-content: space-around;
  list-style: none;
}

.nav__link {
  display: block;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  transition-duration: 0.2s;
}

.nav__link:hover {
  transform: scale(1.2);
}

.nav__search {
  font-size: 1.3em;
}

.button {
  display: inline-block;
  padding: 0.7em 2em;
  border: 2px solid #ffffff;
  border-radius: 2em;
  background-color: rgba(255, 255, 255, 0.2);
  font-family: inherit;
  font-size: inherit;
  line-height: 1em;
  color: inherit;
  transition-duration: 0.2s;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

.header__title, .header__subtitle {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.header {
  height: 100vh;
  box-sizing: border-box;
  background-color: #e8edef;
  background-image: url("../img/header-bg.jpg");
  background-size: cover;
  padding: 50px;
  font-size: 25px;
  color: #ffffff;
}

@media (max-height: 550px) {
  .header {
    height: auto;
  }
}

.header__top {
  display: flex;
  align-items: center;
  padding: 2em;
  background-color: #ffffff;
  font-size: 16px;
  color: #c1b2b1;
}

.header__logo-wrap {
  flex: 2 auto;
  text-align: left;
}

.header__logo {
  height: 1.2em;
  color: #c1b2b1;
}

.header__nav {
  flex: 1 auto;
}

.header__greating {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

@media (max-height: 550px) {
  .header__greating {
    position: static;
    transform: none;
    margin: 2em;
  }
}

.header__title {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 100px;
  letter-spacing: 0.1em;
}

.header__subtitle {
  letter-spacing: 0.2em;
  font-family: "Raleway", sans-serif;
}

.about {
  padding-left: 20px;
  transition: 300ms;
}

@media (min-width: 768px) {
  .about {
    padding-left: 100px;
  }
}

@media (min-width: 1024px) {
  .about {
    padding-left: 150px;
  }
}

.footer {
  font-size: 20px;
  display: flex;
  flex-flow: column;
}

@media (min-width: 1024px) {
  .footer {
    flex-flow: row wrap;
    align-items: center;
  }
}

.footer__item {
  display: flex;
  flex-direction: column;
  padding: 2em;
  box-sizing: border-box;
  transition: 300ms;
}

@media (min-width: 768px) {
  .footer__item {
    flex-direction: row;
    justify-content: space-around;
  }
}

@media (min-width: 1024px) {
  .footer__item {
    flex: 1 0 auto;
  }
}

.footer__additional {
  margin-bottom: 1em;
  transition: 300ms;
}

@media (min-width: 768px) {
  .footer__additional {
    margin-bottom: 0;
  }
}

.footer__title {
  margin: 0;
  padding-bottom: 1em;
  color: #95a9b1;
  text-transform: uppercase;
  white-space: nowrap;
}

.footer__links {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__link {
  padding-bottom: 1em;
  color: #98b8f6;
  white-space: nowrap;
}

.footer__bottom {
  display: flex;
  box-sizing: border-box;
  background-color: #e8edef;
  padding: 1em 1em;
  justify-content: space-between;
  color: #95a9b1;
  font-size: 0.8em;
  transition: 300ms;
}

@media (min-width: 768px) {
  .footer__bottom {
    padding: 1em 3em;
    font-size: 1em;
  }
}

@media (min-width: 1024px) {
  .footer__bottom {
    flex: 1 100%;
  }
}

.footer__copyright {
  flex: 1 1 auto;
  transition: 300ms;
}

.footer__social {
  flex: 0 0 auto;
}

.footer__icon {
  margin-right: 1em;
  transition: 300ms;
}

.footer__icon:last-child {
  margin-right: 0;
}

@media (min-width: 768px) {
  .footer__icon {
    margin-right: 2em;
  }
}

@media (min-width: 1024px) {
  .footer__icon {
    margin-right: 3em;
  }
}
